@use 'variables' as v;

html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*:before, *:after{
    box-sizing: inherit;
}

body{
   font-size: 1.6rem;
   background-color: v.$colorPrincipal;
   font-family: v.$fuentePrincipal;
}

h1,h2,h3,h4{
    line-height: 6rem;
    text-align: center;
    color: v.$colorTipografia;
    margin: 0;
}

h1{font-size: 6rem;}
h2{font-size: 3.8rem;}
h3{font-size: 2.8rem;}
h4{font-size: 2.4rem;}

p{
    font-size: 2rem;
    color: v.$colorTipografia;
    margin: 0;
}

img{
    display: block;
    max-width: 100%;
}

ul{
    list-style: none;
    padding: 0;
}

li{
    padding: 0;
    margin: 0;
}

a{
    font-size: 2rem;
    color: v.$colorTipografia;
    text-decoration: none;
}

.container{
    max-width: 120rem;
    width: 90%;
    margin: 0 auto;
}

button{
    border: none;
}