html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  background-color: #252422;
  font-family: "Roboto", sans-serif;
}

h1, h2, h3, h4 {
  line-height: 6rem;
  text-align: center;
  color: #fff6f6;
  margin: 0;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.8rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: #fff6f6;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 0;
  margin: 0;
}

a {
  font-size: 2rem;
  color: #fff6f6;
  text-decoration: none;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

button {
  border: none;
}

.nav {
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin: 3rem;
    padding: 1.5rem;
  }
}
.nav .nav-cv {
  padding: 0;
  margin-left: 0;
}
.nav .nav_AColor {
  padding: 1rem 2rem;
  font-weight: 700;
}
.nav .cv {
  border: 1px #0091ad solid;
}
.nav .nav_AColor:hover {
  color: #0091ad;
  transition: ease-out 500ms;
}

.perfil {
  padding: 3rem 0 2.5rem;
  display: flex;
}
.perfil img {
  width: 23rem;
  height: 28rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .perfil {
    padding: 5rem 0 2.5rem;
    max-width: 30rem;
    margin: 0;
  }
}

.header .header_Titulo {
  text-align: center;
}
@media (min-width: 768px) {
  .header .header_Titulo {
    text-align: start;
  }
}
.header .header_Subtitulo {
  color: #0091ad;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .header .header_Subtitulo {
    text-align: start;
  }
}

.header_Parrafo {
  text-align: center;
  line-height: 3rem;
  max-width: 80rem;
  min-width: 30rem;
  margin: 1rem;
}
@media (min-width: 768px) {
  .header_Parrafo {
    text-align: start;
  }
}

.iconos_icono p {
  text-align: center;
  font-size: 1.5rem;
}

.iconos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 5rem;
}
@media (min-width: 768px) {
  .iconos {
    justify-content: flex-start;
  }
}
.iconos .iconos_icono {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1.2rem;
}

.sobreMi {
  margin: 15rem auto;
}
.sobreMi h2 {
  padding: 3rem 0;
}
.sobreMi .informacion {
  min-width: 25rem;
}
.sobreMi .informacion p {
  margin-top: 5rem;
  line-height: 3rem;
}

@media (min-width: 1200px) {
  .sobreMi {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
  }
}

@media (min-width: 340px) {
  .habilidades_iconos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}
@media (min-width: 768px) {
  .habilidades_iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
.habilidades_iconos .icono {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  margin: 6rem auto;
  width: 12rem;
  height: 5rem;
}
.habilidades_iconos .icono p {
  text-align: center;
}

.experiencia_content {
  border-radius: 0 3rem;
  padding: 3rem;
}
.experiencia_content h3 {
  width: 100%;
  color: black;
}

.experiencia_content:nth-child(1) {
  background-color: #5b8e7d;
}

.experiencia_content:nth-child(2) {
  background-color: #ac344c;
}

.experiencia {
  padding: 5rem 0;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .experiencia {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.experiencia li {
  list-style: circle;
  color: #fff3b0;
  font-size: 2rem;
}
.experiencia li p {
  margin: 1.5rem 0;
  line-height: 3rem;
}

.contacto {
  position: relative;
  overflow: hidden;
  min-height: 60rem;
  max-height: 65rem;
}
@media (min-width: 768px) {
  .contacto {
    min-height: 60rem;
  }
}
.contacto .contacto_video {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}
@media (min-width: 992px) {
  .contacto .contacto_video {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  video {
    height: auto;
  }
}
@media (min-width: 992px) {
  video {
    width: 100%;
  }
}

.formulario {
  background-color: rgba(253, 252, 252, 0.3);
  border-radius: 2rem;
  max-width: 90rem;
  min-width: 30rem;
  padding: 3rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .formulario {
    max-width: 80rem;
    min-width: 40rem;
  }
}
.formulario .formulario_campo {
  margin: 1rem;
}
.formulario .formulario_campo p {
  font-size: 1.5rem;
  text-align: center;
}
.formulario .element, .formulario .element::placeholder {
  color: #d4bfbf;
  font-weight: 700;
}
.formulario .element {
  background-color: rgba(0, 0, 0, 0.4);
  width: 100%;
  padding: 1rem 0;
  padding-left: 1rem;
  margin: 1rem 0;
}

.campoEnviar {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.campoEnviar .btnEnviar {
  background-color: #0091ad;
  color: #fdfcfc;
  cursor: pointer;
  border-radius: 1rem;
  letter-spacing: 0.5rem;
}
@media (min-width: 768px) {
  .campoEnviar .btnEnviar {
    width: 15rem;
  }
}
.campoEnviar .btnError {
  cursor: not-allowed;
  opacity: 70%;
}

.footer {
  text-align: center;
  padding: 3rem 0;
}

.iconos-inferirores {
  margin-bottom: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.iconos-inferirores .iconos_icono {
  padding: 1rem;
}
.iconos-inferirores .iconos_icono svg {
  width: 6rem;
  height: 6rem;
}

.spinner {
  display: none;
  margin: 0 auto;
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border-left-color: #09f;
  animation: spin 1s ease infinite;
}

.mostrarSpinner {
  display: flex;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}