@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;

.contacto{
    position: relative;
    overflow: hidden;
    min-height: 60rem;
    max-height: 65rem;
    @include m.tablet{
        min-height: 60rem;
    }

    .contacto_video{
        position: absolute;
        background-color: rgba($color: v.$colorNegro, $alpha: 0.3);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        @include m.desktop{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        z-index: 1;
    }

}

video{
    width: auto;
    display: block;
    
    @include m.tablet{
       height: auto;
    }

    @include m.desktop{
        width: 100%;
    }
}