@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;

.experiencia_content{
    border-radius: 0 3rem;
    padding:3rem;
    h3{
        width: 100%;
        color: black;
    }
}

.experiencia_content:nth-child(1){
    background-color: #5b8e7d;
}

.experiencia_content:nth-child(2){
    background-color: #ac344c;
}

.experiencia{
    padding: 5rem 0;
    margin-bottom: 5rem;
    @include m.tablet{
        @include m.grid(2, 1rem);
    }    
    li{
        list-style:circle;
        color: v.$colorStyle;
        font-size: 2rem;
        p{
            margin: 1.5rem 0;
            line-height: 3rem;
        }
    }
}
