@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;

.formulario{
    background-color: rgba($color: v.$colorBlanco, $alpha: 0.3);
    border-radius: 2rem;
    max-width: 90rem;
    min-width: 30rem;
    padding: 3rem;
    z-index: 1;

    @include m.tablet{
        max-width: 80rem;
        min-width: 40rem;
    }
  

    .formulario_campo{
        margin: 1rem;
        p{
            font-size: 1.5rem;
            text-align: center;
        }
    }

    .element, .element::placeholder{
        color: darken($color: v.$colorBlanco, $amount: 20%);
        font-weight: 700;
    }
    
    .element{
        background-color: rgba($color: v.$colorNegro, $alpha: 0.4);
        width: 100%;
        padding: 1rem 0;
        padding-left: 1rem;
        margin: 1rem 0;
    }

}


.campoEnviar{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    
    .btnEnviar{
        background-color: v.$colorCarmesiEnlace;
        color: v.$colorBlanco;
        cursor: pointer;
        border-radius: 1rem;
        letter-spacing: 0.5rem;
        @include m.tablet{
            width: 15rem;
        }
    }
    .btnError{
        cursor:not-allowed;
        opacity: 70%;
    }
}
