@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;

.iconos{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 5rem;
    @include m.tablet{
        justify-content: flex-start;
    }
    .iconos_icono{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0 1.2rem;
    }
}