@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;

.nav{
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-top: 3rem;
    @include m.tablet{
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        margin: 3rem;
        padding: 1.5rem;
    }

    .nav-cv{
        padding: 0;
        margin-left: 0;
    }
    .nav_AColor{
        padding: 1rem 2rem;
        font-weight: 700;
    }
    
    .cv{
        border: 1px v.$colorCarmesiEnlace solid;
    }


    .nav_AColor:hover{
        color: v.$colorCarmesiEnlace;
        transition: ease-out 500ms;
    }
}