@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;


.header{
    .header_Titulo{
        text-align: center;
        @include m.tablet{
            text-align: start;
        }
    }
    .header_Subtitulo{  
        color: v.$colorCarmesiEnlace;
        margin: 0;
        text-align: center;
        @include m.tablet{
            text-align: start;
        }
    }
}

.header_Parrafo{
    text-align: center;
    line-height: 3rem;
    max-width: 80rem;
    min-width: 30rem;
    margin: 1rem;
    @include m.tablet{
        text-align: start;
    }
}

.iconos_icono{
    p{
        text-align: center;
        font-size: 1.5rem;
    }
}