@use '../base/variables.scss' as v;
@use '../base/mixins.scss' as m;

.sobreMi{
    margin: 15rem auto;

    h2{
        padding: 3rem 0;
    }
    .informacion{
        min-width: 25rem;
        p{
            margin-top: 5rem;
            line-height: 3rem;
        }
    }
}

.sobreMi{
   @include m.xldesktop{
    @include m.grid(2, 0rem);
   }
}


.habilidades_iconos{
    @include m.telefono{
     @include m.grid(2, 0);
    }

   @include m.tablet{
    @include m.grid(3, 1rem);
   }

    .icono{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 0.5rem;
            margin: 6rem auto;
            width: 12rem;
            height: 5rem;
            p{
                text-align: center;
            }
    }
}